@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  flex-direction: column;
  width: 100vw;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  object-fit: cover;
  pointer-events: none;
  z-index: -1;
  width: 100vmax;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
  position: relative;
  top: 20px;
  left: 10%;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

h3:hover {
  color: brown;
}

p {
  color: #fff5ee;
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  color: #fff5ee;
  font-size: 24px;
}

p.small {
  color: #272502;
  font-size: 15px;
  font-weight: 400;
}

hr {
  margin: 2.5rem;
}

li {
  color: #fff5ee;
  line-height: 1.7;
}

li::marker {
  color: #f5f5f5;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #151d41;
}

.container {
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
}

img.socialIcon {
  width: 30px;
  height: 30px;
}

img.socialIcon:hover {
  filter: grayscale() brightness(0);
}

@media (width <= 1200px) {
  h1 {
    font-size: 5rem;
  }

  .container {
    grid-template-columns: 1fr;
    margin: 20px;
  }

  .container .box {
    text-align: center;
    width: 100%;
  }

  section img.background {
    width: 100%;
  }
}

@media (width <= 768px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 1.8rem;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .container .box {
    padding: 20px;
  }

  section img.background {
    width: 100%;
    height: 60%;
  }

  p {
    font-size: 16px;
  }
}

@media (width <= 480px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  p {
    font-size: 14px;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .container .box {
    width: 100%;
  }

  section img.background {
    width: 100%;
    height: 50%;
  }
}
