@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  font-family: "Montserrat", sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
}

div#main {
  display: flex;
  flex-direction: column;
  width: 100vw;
}

section {
  position: relative;
  width: 100vw;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  left: 50%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100vmax;
  z-index: -1;
}

h1 {
  font-family: "Cormorant Garamond", serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: 0.8;
  margin: 0;
  position: relative;
  top: 20px; /* Move the name down */
  left: 10%; /* Move the name to the left */
}

h2 {
  font-size: 2rem;
  font-weight: 400;
  margin: 0;
  padding-top: 1rem;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
}
h3:hover {
  color: brown;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
  color: seashell;
}

p.large {
  font-size: 24px;
  color: seashell;
}

p.small {
  font-size: 15px;
  font-weight: 400;
  color: rgb(39, 37, 2);
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
  color: seashell;
}

li::marker {
  color: whitesmoke;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #151d41;
}

.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
}

.container .box {
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

img.socialIcon:hover {
  filter: grayscale(100%) brightness(0);
}

/* Responsiveness */
@media (max-width: 1200px) {
  h1 {
    font-size: 5rem;
  }

  .container {
    grid-template-columns: 1fr;
    margin: 20px;
  }

  .container .box {
    width: 100%;
    text-align: center;
  }

  section img.background {
    width: 100%;
  }
}

@media (max-width: 768px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 1.8rem;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .container .box {
    padding: 20px;
  }

  section img.background {
    width: 100%;
    height: 60%;
  }

  p {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  p {
    font-size: 14px;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .container .box {
    width: 100%;
  }

  section img.background {
    width: 100%;
    height: 50%;
  }
}
